// Polyfills
import "core-js/stable"
import "regenerator-runtime/runtime"
// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue"
import App from "./App"
import { ColorThemePlugin } from "../services/vuestic-ui"
import store from "../store/index"
import router from "../router/index"
import { VuesticPlugin } from "../services/vuestic-ui/components"
import "../i18n/index"
import VueClipboard from "vue-clipboard2"

import "../metrics"
// import '../registerServiceWorker'

Vue.use(VuesticPlugin)
Vue.use(VueClipboard)

Vue.use(ColorThemePlugin, {})

Vue.prototype.$time = {
  getLocale: () => {
    return +new Date()
  },
  getPacific: () => {
    let offset = new Date().getTimezoneOffset() * 60 * 1000
    return getLocale() + offset
  },
  getExpiration: (from, additionalMinutes) => {
    let min = typeof additionalMinutes === 'number' ? additionalMinutes : 0
    return from + 1000 * 60 * min
  },
  localeToPacific: locale => {
    let offset = new Date().getTimezoneOffset() * 60 * 1000
    return locale + offset
  },
  pacificToLocale: pacific => {
    let offset = new Date().getTimezoneOffset() * 60 * 1000
    return pacific - offset
  },
}

router.beforeEach((to, from, next) => {
  store.commit("setLoading", true)
  next()
})

router.afterEach((to, from) => {
  store.commit("setLoading", false)
})

/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  store,
  render: h => h(App)
})
